@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #f2f2f2;
  font-family: "Open Sans", sans-serif;
}
.searchBody {
  width: 70%;
  position: absolute;
  left: 15%;
  height: auto;
}
.searcher {
  width: 100%;
  position: relative;
  display: flex;
}

.searcherTerm {
  width: 100%;
  border: 3px solid #8ece86;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #9dbfaf;
}

.searcherTerm:focus {
  color: black;
}

.searcherButton {
  width: 40px;
  height: 36px;
  border: 1px solid #8ece86;
  background: #8ece86;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.center {
  display: flex;
  justify-content: center;
}
.wrap {
  width: 50%;
  position: relative;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

